.section-title {

  $block-name:                &; // #{$block-name}__element

  display: inline-block;
  margin: 0;
  margin-bottom: 70px;
  padding-left: 25px;
  padding-right: 25px;

  font-size: px-to-em(16);
  line-height: 2.2em;
  font-weight: 700;
  text-transform: uppercase;

  border-bottom: 2px solid $gray-lightest;
}
